/**
 * 权限入口文件
 * @param {String}  authType -权限种类
 */

import MpAuth from './mp.js' //微信小程序










export default class Auth {

	constructor(authType) {
		this.authType = authType
	}

	async check() {
		let state = 0

		state = await new MpAuth(this.authType).checkAuth()







		return Promise.resolve(state)
	}

}
